import React, { useState,useRef, useEffect } from 'react'
import '../index.css'
import Editor from '../components/Editor'
import logo from '../asset/sync.png'
import Client from '../components/Client'
import { initSocket } from '../socket'
import {useNavigate, useLocation} from 'react-router-dom'
import toast from 'react-hot-toast'

const EditorPage = () => {
  const navigate =useNavigate();
  const socketRef=useRef(null);
  const location=useLocation();
  const [clients,setClient]=useState([]);
  const codeRef=useRef();

  useEffect(()=>{

  if(!location.state){
    toast.error('Try again');
    navigate('/')
    return;
  }
    async function init(){
     

      socketRef.current=await initSocket();
      socketRef.current.on('connect_error',(err)=>{handelErrors(err)})
      socketRef.current.on('connect_failed',(err)=>{handelErrors(err)})

      function handelErrors(err){
        console.log(err)
        toast.error('Socket Connection Failed, Try again');
        navigate('/')
      }
      

      socketRef.current.emit('join',{
        roomId:location.pathname.split('/').at(-1),
        userName:location.state.userName,
      })

      socketRef.current.on('joined',({clients,userName,socketId})=>{
        if(userName!==location.state.userName){
          toast.success(`${userName} Joined`);
          socketRef.current.emit('code-change',{
            roomId:location.pathname.split('/').at(-1),
            code:codeRef.current,
          })
        }
        setClient(clients);

      })
      socketRef.current.on('disconnected',({socketId,userName})=>{
        toast.success(`${userName} Left`);
        setClient((prev)=>{
          return prev.filter(client => client.socketId!==socketId);
        })

      })

    }
    
    init();


    //clearing
    return ()=>{
      socketRef.current.disconnect();
      socketRef.current.off('joined')
      socketRef.current.off('disconnected');
    }

  },[])


  async function copyRoomId(){

    try{
      await navigator.clipboard.writeText(location.pathname.split('/').at(-1));
      toast.success('Room ID Copied');
    }
    catch(err){
      toast.error('Error');
    }
  }

  async function leaveRoom(){
    navigate('/');

  }

  return (
    <div className='editor-container'>
     
      <div className='editor-left'>
        <img className='editor-logo' src={logo}/>
        <div className='line'></div>

        <div className='connected'>
            <h3>Connected</h3>
            <div className='client-list'>
              {clients.map((client)=>{
                return (<Client key={client.socketId} username={client.userName}/>)
              })}
            </div>
        </div>

        <div className='line'></div>
        <div className='controls'>
          <button onClick={copyRoomId} className='btn control-btn'>Copy ROOM ID</button>
          <button onClick={leaveRoom} className='btn control-btn leave-btn'>Leave</button>

        </div>
      </div>
      <div className='editor-right'>
              <Editor codeRef={codeRef} socketRef={socketRef} roomId={location.pathname.split('/').at(-1)}/>
      </div>
    </div>
  )
}

export default EditorPage