import React,{useState} from 'react';
import '../index.css';
import { v4 as uuid } from 'uuid';
import toast from 'react-hot-toast';
import { useNavigate } from "react-router-dom";
import logo from '../asset/sync.png'



const Home = () => {
  const [roomId,setRoomID]=useState('');
  const [userName,setUserName]=useState('');
  const navigate=useNavigate();

  const createNewRoom=(e)=>{
    const id=uuid();
    setRoomID(id);
    toast.success('New Room Created ',{
      position: 'top-right',
    });
  }

  const joinBtn=(e)=>{
    if(!roomId || !userName) {
      toast.error('Room Id & Username is required');
      return;
    }

    navigate(`/editor/${roomId}`,{
      state :{
        userName:userName
      }
    });
  }
  return (
   <div className='homepage-container'>
    <div className='form-container'>
        <img src={logo} alt='logo'/>
        <h4>Paste Room ID </h4>
        <form>
          <input type= 'text'
           className='input-box'
           required
            placeholder='ROOM ID' 
            onChange={(e)=>{setRoomID(e.target.value)}} 
            value={roomId}
          />
          <input type= 'text'
           className='input-box'
           required
            placeholder='USERNAME'
            onChange={(e)=>{
              setUserName(e.target.value)
            }}
            value={userName}
          />
          <button onClick={joinBtn} className='btn join-btn'>Join</button>
          <p>don't have room id ? create &nbsp; 
            <span onClick={createNewRoom} className='new-room-btn'href=''>new room</span>
          </p>
        </form>
    </div>
   </div>
  )
}

export default Home